.card
  {
    /* border: 1px solid #000; */
    display: flex;
    margin: 50px auto;
    width: 250px;
    height: 200px;
    align-items: center;
    justify-content: space-between;
    border-radius: 25px;
    flex-wrap: wrap;
    box-shadow: 10px 10px 10px rgb(190, 188, 188), 5px 5px 20px gray;
  }

img
  {
    text-decoration: none;
    border: transparent;
    margin: auto;
    border-radius: 20px;
    width: 200px;
    height: 100px;
    /* box-shadow: 5px 5px 10px rgb(160, 150, 150); */
  }

h3
  {
    margin: 10px auto;
    font-size: 20px;
    width: 100%;
    text-align:center;
    color: rgb(236, 226, 226);

  }  

.card:hover
  {
    background: gray;
    color: black;
  }